$backgroundColor: #f5f5f5;

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: $backgroundColor;
  height: 100%;
  font-size: 20px;
  color: rgb(112, 1, 1);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: auto;


  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  footer {
    background: darken($backgroundColor, 50);
    border-top: 1px solid #eee;
    padding: 1em 3em;
    margin: 0;
  }

  header {
    background: darken($backgroundColor, 75);
    border-bottom: 1px solid rgb(4, 160, 172);
    color: rgb(20, 151, 184);
    padding: 0;
    margin: 0;
    text-align: center;
  }
  ui{
    padding: 1em 3em;
    margin: 0;
    text-align: center;
    // display:flex;
    font-size: 20px;



  }
 
}